(function() {
  'use strict';

  angular
    .module('frontend', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ngMaterial', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('frontend')
    .directive('myNavbar', myNavbar);

  /** @ngInject */
  function myNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(auth) {
      var vm = this;
      vm.status= auth.getStatus();
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .service('device', Device);

  /** @ngInject */
  function Device($http) {
    var vm = this;
    vm.getDevices = getDevices;
    vm.getDevice = getDevice;
    vm.updateDevice = updateDevice;


    function getDevices() {
      return $http.get('/api/device').then(function (resp) {
        return resp.data;
      });
    }

    function getDevice(device_name) {
      return $http.get('/api/device/' + device_name).then(function (resp) {
        return resp.data
      });
    }

    function updateDevice(device) {
      return $http.post('/api/device/' + device.name, device).then(function(resp){
        return resp.data;
      });
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .service('auth', auth);

  /** @ngInject */
  function auth($http) {
    var vm = this;
    vm.getForm = getForm;
    vm.login = login;
    vm.updateStatus = updateStatus;
    vm.getStatus = getStatus;
    vm.status = {'loggedIn': false, 'user': null};

    updateStatus();

    function getForm() {
      return $http.get('/api/login').then(function (response) {
        return response.data.form;
      });
    }

    function login(form) {
      return $http.post('/api/login', form).then(function () {
        status.loggedIn = true;
      });
    }

    function updateStatus() {
      return $http.get('/api/user').then(function (resp) {
        vm.status.user = resp.data;
        vm.status.loggedIn = resp.data.user_id != undefined;
        return vm.status;
      });
    }

    function getStatus() {
      return vm.status;
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .service('animations', Animations);

  /** @ngInject */
  function Animations($http) {
    var vm = this;
    vm.getAnimations = getAnimations;
    vm.getAnimation = getAnimation;


    function getAnimations() {
      return $http.get('/api/animation', {cache: true}).then(function (resp) {
        return resp.data;
      });
    }

    function getAnimation(key) {
      return getAnimations().then(function (animations) {
        var found= null
        angular.forEach(animations, function (v) {
          if (v.name == key) {
            found= v;
          }
        });
        return found;
      });
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($state,$scope, auth, device) {
    var vm = this;
    vm.gotoDevice = gotoDevice;
    vm.devices = [];

    activate();

    function activate() {
      auth.updateStatus().then(function (status) {
        if (!status.loggedIn) {
          $state.go('login');
        }
        device.getDevices().then(function (devices) {
          vm.devices = devices;
        });
      });
    }


    function gotoDevice(key) {
        $state.go('device', {key: key});
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($log, $state, auth) {
    var vm = this;
    vm.form = undefined;
    vm.error = undefined;

    vm.login = login;

    activate();

    function activate() {
      auth.getForm().then(function (data) {
        vm.form = data
      });
    }

    function login() {
      return auth.login(vm.form).then(function () {
        vm.error = undefined;
        $state.go('home');
      }, function (data) {
        vm.error = data.data.error;
      });
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .controller('DeviceController', DeviceController);

  /** @ngInject */
  function DeviceController($stateParams, device, animations ) {
    var vm = this;
    vm.device = undefined;
    vm.animations = undefined;
    vm.animation = undefined;
    vm.update = update;
    vm.updateAnimation = updateAnimation;

    activate();

    function activate() {
      device.getDevice($stateParams.key).then(function (device) {
        vm.device = device;
          vm.animation = device.animation.name;
      });
      animations.getAnimations().then(function (animations) {
        vm.animations = animations;
      });
    }

    function update() {
      if (vm.device != undefined) {
        device.updateDevice(vm.device).then(function (device) {
          vm.device = device;
          vm.animation = device.animation.name;
        });
      }
    }

    function updateAnimation() {
      if (vm.device != undefined) {
        animations.getAnimation(vm.animation).then(function(animation){
          vm.device.animation = animation;
        });
      }
    }
  }
})();

/* global csrftoken:'' */
(function () {
  'use strict';

  angular
    .module('frontend')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $http) {
    $http.defaults.headers.post['X-CSRFToken'] = csrftoken;
    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('frontend')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
      .state('login', {
        url: "/login",
        templateUrl: "app/login/login.html",
        controller: 'LoginController',
        controllerAs: 'vm'
      })
      .state('device', {
        url: "/device/:key",
        templateUrl: "app/device/device.html",
        controller: 'DeviceController',
        controllerAs: 'vm'
      });

    $urlRouterProvider.otherwise('/');
  }

})();


